import React from 'react'
import AddSection from './components/add-section'
import InfoSection from './components/info-section'
import RelativeSection from './components/relative-section'
import Getnode from './components/get-data-node'
import NavBar from './components/nav-section'

import 'materialize-css'
import './app.css'
import axios from 'axios'
import {appendScript} from './rm'

import { BrowserRouter as Router, Route } from 'react-router-dom'

class App extends React.Component {
  
  state = {
    apiResponce: "",
    dataBtc: [],
    dataEth: [],
    dataBch: [],
    dataDoge: []
  }
  
  newTickerBtc = {
      name: "",
      price: ""
  }
  newTickerEth = {
    name: "",
    price: ""
}
newTickerBch = {
  name: "",
  price: ""
}

  newTickerDoge = {
    name: "",
    price: ""
}

addItem = async(text) => { 
    text ? this.newTickerBtc.name = text : this.newTickerBtc.name = "BTC"
    if (text === 'BTC'|| this.newTickerBtc.name === "BTC") {
    let url = `https://min-api.cryptocompare.com/data/price?fsym=${this.newTickerBtc.name}&tsyms=USD&api_key=0816c2723876b3928aa5bbb260dbb3ed7a35e34bf2facc22d938d27ec1ee8c8e`
    setInterval(async() => {
       const {data} = await axios.get(url);
       this.newTickerBtc.price = data.USD
           this.setState({
             dataBtc: this.newTickerBtc
         })  
     }, 1000); 
   }
   if (text === 'ETH') {
    text ? this.newTickerEth.name = text : this.newTickerEth.name = ""
    let url = `https://min-api.cryptocompare.com/data/price?fsym=${this.newTickerEth.name}&tsyms=USD&api_key=0816c2723876b3928aa5bbb260dbb3ed7a35e34bf2facc22d938d27ec1ee8c8e`

    setInterval(async() => {
      const {data} = await axios.get(url);
       this.newTickerEth.price = data.USD
        this.setState({
          dataEth: this.newTickerEth
      }) 
  }, 1000);
   }
   if (text === 'BCH') {
    text ? this.newTickerBch.name = text : this.newTickerBch.name = ""
    let url = `https://min-api.cryptocompare.com/data/price?fsym=${this.newTickerBch.name}&tsyms=USD&api_key=0816c2723876b3928aa5bbb260dbb3ed7a35e34bf2facc22d938d27ec1ee8c8e`

    setInterval(async() => {
      const {data} = await axios.get(url);
       this.newTickerBch.price = data.USD
        this.setState({
          dataBch: this.newTickerBch
      }) 
  }, 1000);
   }
   if (text === 'DOGE') {
    text ? this.newTickerDoge.name = text : this.newTickerDoge.name = ""
    let url = `https://min-api.cryptocompare.com/data/price?fsym=${this.newTickerDoge.name}&tsyms=USD&api_key=0816c2723876b3928aa5bbb260dbb3ed7a35e34bf2facc22d938d27ec1ee8c8e`

    setInterval(async() => {
      const {data} = await axios.get(url);
       this.newTickerDoge.price = data.USD
        this.setState({
         dataDoge: this.newTickerDoge
      }) 
  }, 1000);
   }
   
 }
//   callApi(){
//    fetch("http://localhost:5000/testApi")
//          .then(res => res.text())
//          .then(res => this.setState({apiResponce:res}))
//  }
 componentDidMount(){
   this.addItem()
   //this.callApi()
   appendScript()
 }

render() {  
  const {dataBtc,dataEth,dataBch,dataDoge } = this.state
   return (
  <div className="container mx-auto flex flex-col items-center bg-gray-100 p-4">
    <div className="container">
    
    <Router>
     <NavBar />
     
     <Route path="/source" component={() => { 
     window.location.href = 'https://www.cryptocompare.com/'; 
     return null; }}/>

    </Router>
    
      <div className="w-full my-4"></div>
      <AddSection onItemAdded={this.addItem}/>
      <hr className="w-full border-t border-gray-600 my-4" />
      <InfoSection dataBtc={dataBtc} dataEth={dataEth} dataBch={dataBch} dataDoge={dataDoge} addItem={this.addItem}/>
      <hr className="w-full border-t border-gray-600 my-4" />
      <Getnode />
      <RelativeSection />
  </div>
  </div>
);
};
}

export default App;
