import React from 'react'


import SectionZero from '../comp-section/section-0';
import SectionOne from '../comp-section/section-1';
import SectionTwo from '../comp-section/section-2';
import SectionThree from '../comp-section/section-3';

//import {appendScript} from '../rm'

export default class InfoSection extends React.Component {

state = {
    btc: '',
    eth: '',
    bch: '',
    doge: ''
}    

componentDidMount(){
    let sections = document.getElementsByTagName('section'),
    btn     = document.getElementsByTagName('button');
        for(let i=0; i<sections.length; i++){
             btn[i+1].addEventListener('click', ()=>{
                sections[i].style.display = 'none';
       })
       sections[i].addEventListener('click', ()=>{
         for(let i=0; i<sections.length; i++){
          sections[i].classList.remove = 'border-purple-800';
         }
         sections[i].classList.add = 'border-purple-800'; 
    })
  }   
   //appendScript()
}

render() {
 
    const { dataBtc,dataEth,dataBch, dataDoge } = this.props
    let sections = document.getElementsByTagName('section');
   
    if(dataBtc.name === 'BTC') {
        this.btc = dataBtc.price    
        sections[0].style.display = 'block';
        sections[0].style.border = '3px solid #44337a'; 
     }
    if(dataEth.name === 'ETH') {
        this.eth = dataEth.price  
          sections[1].style.display = 'block';
         }
    if(dataBch.name === 'BCH') { 
        this.bch = dataBch.price 
         sections[2].style.display = 'block'; 
        }  
    if(dataDoge.name === 'DOGE') {
        this.doge = dataDoge.price 
         sections[3].style.display = 'block';
    }  

    return (
        
        <dl className="mt-5 grid grid-cols-1 gap-5 sm:grid-cols-3">
            <SectionZero data={this.btc}/>
            <SectionOne data={this.eth}/>
            <SectionTwo data={this.bch}/>
            <SectionThree data={this.doge}/>
        </dl>
            
        )
  
}
}